    import styled from 'styled-components';
    import MessageInfo from './MessageInfo';

    const BotMessageWrapper = styled.div`
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin: 10px;
    `;

    const BotMessageText = styled.p`
      background-color: ${props => props.theme.colorWhiteSmoke100};
      color: ${props => props.theme.colorDarkSlateBlue};
      padding: 10px;
      border-radius: ${props => props.theme.brMini} ${props => props.theme.brMini} ${props => props.theme.brMini} 0;
      //width: 404px;
      min-height: 24px;
      max-width: 450px;
      word-wrap: break-word;
    `;

    function BotMessage({ message, sender, timestamp }) {
        return (
            <BotMessageWrapper>
                <MessageInfo sender={sender} timestamp={timestamp} />
                <BotMessageText>{message}</BotMessageText>
            </BotMessageWrapper>
        );
    }

    export default BotMessage;
