import React, { useState } from 'react';
import styled from 'styled-components';
import sendIcon from '../assets/images/btn-send.svg';

const InputAreaWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: ${props => props.theme.colorWhite};
`;

const TextInput = styled.input`
  flex-grow: 1;
  margin-right: 15px;
  padding: 10px;
  border-radius: ${props => props.theme.brMini};
  border: 1px solid ${props => props.theme.colorLightSteelBlue100};
  box-sizing: border-box;
  font-size: ${props => props.theme.bodyRegular1Size};
`;

const SendButton = styled.div`
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  background-color: #f5f5f5;
  border-radius: 50%;
  padding: 5px;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &::before {
    content: "";
    display: block;
    width: 12px;  
    height: 15px; 
    background: url(${sendIcon}) no-repeat center center;
    background-size: contain;
  }
`;
function InputArea({ onSend }) {
    const [message, setMessage] = useState('');

    const handleSend = () => {
        if (message.trim() !== '') {
            onSend(message.trim());
            setMessage('');
        }
    };

    const handleKeyPress = (event) => {
        if (event.key === 'Enter' && !event.shiftKey) {
            event.preventDefault();
            handleSend();
        }
    };

    return (
        <InputAreaWrapper>
            <TextInput
                value={message}
                onChange={e => setMessage(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="Ingresa una nueva consulta"
            />
            <SendButton onClick={handleSend} />
        </InputAreaWrapper>
    );
}

export default InputArea;
