import React, { useState } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import ChatHeader from './ChatHeader';
import ChatWindow from './ChatWindow'; // Importamos el nuevo componente ChatWindow
import LoadingSpinner from "./LoadingSpinner"; // Importa el componente LoadingSpinner

const ChatbotWrapper = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
`;

const LoadingWrapper = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
`;

function Chatbot() {
    const [isLoading, setIsLoading] = useState(false);
    const [messages, setMessages] = useState([]);

    const handleSend = (message) => {
        setIsLoading(true);
        setMessages(prev => [...prev, { sender: 'User', text: message, timestamp: new Date() }]);

        axios.post('http://api.mt2.cl/ask', {
            message: message
        })
            .then(function (response) {
                setIsLoading(false);
                const botResponse = response.data.message;
                setMessages(prev => [...prev, { sender: 'Sofia', text: botResponse, timestamp: new Date() }]);
            })
            .catch(function (error) {
                setIsLoading(false);
                console.log(error);
            });
    };

    return (
        <ChatbotWrapper>
            <ChatHeader
                image1="/profile.svg"
                image2="/profile.svg"
                botName={"Sofa"}
                botStatus={isLoading ? "Cargando..." : "Online"}
            />
            <ChatWindow messages={messages} onSend={handleSend} />
            {isLoading && (
                <LoadingWrapper>
                    <LoadingSpinner />
                </LoadingWrapper>
            )}
        </ChatbotWrapper>
    );
}

export default Chatbot;
