import styled from 'styled-components';

const HeaderWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: ${props => props.theme.colorWhiteSmoke100};
  border-radius: ${props => props.theme.brMini} ${props => props.theme.brMini} 0 0;
  width: 792px;
  margin: 0 auto;
`;

const ProfileWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const ProfilePicture = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: ${props => props.theme.colorb35a};
  border: 3px solid ${props => props.theme.colorb35a};
  box-sizing: border-box;
`;

const Image1 = styled.img`
  position: absolute;
  left: 4px;
  width: 12px;
  height: 7px; 
`;

const Image2 = styled.img`
  position: absolute;
  right: 4px;
  width: 12px; 
  height: 7px; 
`;

const BotInfo = styled.div`
  margin-left: 10px;
`;

const BotName = styled.p`
  margin: 0;
  font-weight: bold;
  font-size: ${props => props.theme.h5Size};
  color: ${props=> props.theme.colorDarkSlateBlue};
`;

const StatusWrapper = styled.div`
  display: flex;
  align-items: center;
`;

const BotStatus = styled.p`
  margin: 0;
  color: ${props => props.theme.colorGreen};
  color: ${props => props.theme.colorb35a};
  font-size: ${props => props.theme.bodyRegular1Size};
`;

const StatusCircle = styled.span`
  display: inline-block;
  border-radius: 50%;
  background-color: ${props => props.theme.colorb35a};
  width: 9px;
  height: 9px;
  margin-left: 5px;
  margin-right: 5px;
`;

const DateLine = styled.p`
  margin: 0;
  font-weight: normal;
  font-size: ${prop=> prop.theme.bodyRegular1Size};
  color: ${prop=> prop.theme.colorLightSteelBlue100};
`;

const TimeLine = styled.p`
  margin: 0;
  font-weight: 600; 
  font-size: ${prop=> prop.theme.bodyRegular1Size};
  color: ${prop=> prop.theme.colorDarkSlateBlue};
  
`;


const DateTime = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end; 
  justify-content: center;
`;

function ChatHeader({ image1, image2, botName, botStatus }) {

    const dateObject = new Date();


    const dateOptions = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    const timeOptions = {hour: '2-digit', minute: '2-digit'}


    const formattedDate = dateObject.toLocaleDateString('es-ES', dateOptions);
    const formattedTime = dateObject.toLocaleTimeString('es-ES', timeOptions);


    return (
        <HeaderWrapper>
            <ProfileWrapper>
                <ProfilePicture>
                    <Image1 src={image1} alt="Image 1" />
                    <Image2 src={image2} alt="Image 2" />
                </ProfilePicture>
                <BotInfo>
                    <BotName>{botName} </BotName>
                    <StatusWrapper>
                        <StatusCircle />
                        <BotStatus> {botStatus}</BotStatus>
                    </StatusWrapper>
                </BotInfo>
            </ProfileWrapper>
            <DateTime>
                <TimeLine>{formattedTime}</TimeLine>
                <DateLine>{formattedDate}</DateLine>
            </DateTime>
        </HeaderWrapper>
    );
}

export default ChatHeader;
