import styled from 'styled-components';
import MessageInfo from './MessageInfo';

const UserMessageWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 10px;
`;


const UserMessageText = styled.p`
  background-color: ${props => props.theme.colorDarkSlateBlue};
  color: white;
  padding: 10px;
  border-radius: ${props => props.theme.brMini} ${props => props.theme.brMini} 0 ${props => props.theme.brMini};
  min-width: 100px;
  max-width: 450px;
  min-height: 15px;
`;



function UserMessage({ message, sender, timestamp  }) {
    return (
        <UserMessageWrapper>
            <MessageInfo sender={sender} timestamp={timestamp} />
            <UserMessageText>{message}</UserMessageText>
        </UserMessageWrapper>
    );
}

export default UserMessage;
