import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import BotMessage from './BotMessage';
import UserMessage from './UserMessage';
import InputArea from "./InputArea";

const ChatWindowWrapper = styled.div`
  display: flex;
  flex-direction: column;
  padding: 20px 20px 20px 20px;
  height: calc(100vh - 300px);
  overflow-y: auto;
  //border: 1px solid #ddd;
  border-radius: 0 0 ${props => props.theme.brMini} ${props => props.theme.brMini} ;
  background-color: white;
  box-shadow: 0 16px 40px rgba(112, 144, 176, 0.2);
  width: 792px;
  margin: 0 auto;
`;

const MessagesWrapper = styled.div`
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  flex-grow: 1;
`;

function ChatWindow({ messages, onSend }) {
    const messagesEndRef = useRef(null);

    useEffect(() => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }, [messages]);

    return (
        <ChatWindowWrapper>
            <MessagesWrapper>
                {messages.map((msg, index) =>
                    msg.sender === 'Sofia' ? (
                        <BotMessage key={index} message={msg.text} sender={msg.sender} timestamp={msg.timestamp}/>
                    ) : (
                        <UserMessage key={index} message={msg.text} sender={msg.sender} timestamp={msg.timestamp}/>
                    )
                )}
                <div ref={messagesEndRef} />
            </MessagesWrapper>
            <InputArea onSend={onSend} />
        </ChatWindowWrapper>
    );
}

export default ChatWindow;
