import styled from 'styled-components';

const MessageInfoWrapper = styled.p`
  margin: 0;
  font-size: ${props => props.theme.bodySemiBoldSize};
  color: ${props=> props.theme.colorLightSteelBlue100};
  font-family: ${props => props.theme.bodyRegular};
`;

function MessageInfo({ sender, timestamp }) {

    const dateObject = new Date();
    const timeOptions = {hour: '2-digit', minute: '2-digit'}
    const formattedTime = dateObject.toLocaleTimeString('es-ES', timeOptions);


    return (
        <MessageInfoWrapper>{sender} // {formattedTime}</MessageInfoWrapper>
    );
}

export default MessageInfo;
